import '../App.css';
import React from 'react';
import * as ReactBootStrap from 'react-bootstrap';

function NavInicial(){
    // return(
    //     <nav>
    //         <ul className='nav_links'>
    //         <ReactBootStrap.Button href="saul">Better Call Saul</ReactBootStrap.Button>
    //         <ReactBootStrap.Button href="breaking">Breaking Bad</ReactBootStrap.Button>
    //         </ul>
    //     </nav>
    // );

    const goToPage = (path) => {
        window.location = `/${path}`;
    }

    return(
        <nav>
            <ul className='nav_links'>
                <ReactBootStrap.Button onClick={() => goToPage('saul')}>Better Call Saul</ReactBootStrap.Button>
                <ReactBootStrap.Button onClick={() => goToPage('breaking')}>Breaking Bad</ReactBootStrap.Button>
            </ul>
        </nav>
    );


}

export default NavInicial;